body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{ 
  margin:0px; 
  padding:0px; 
  border-radius: 0px; 
  box-sizing: border-box; 
  font-size: 110%; 
} 

#root{ 
  text-align:center; 
} 

.calculator-title{ 
	font-size:30px; 
	background: #fff; 
	width: 400px; 
	padding: 10px 10px; 
	margin: 0 auto; 
	margin-top: 20px; 
	margin-bottom: 20px; 
	border-radius: 2px; 
	border: 2px solid black; 
	color: #4CAF50; 
} 

.mainCalc{ 
  margin:0px; 
  padding:0px; 
  border-radius: 0px; 
  box-sizing: border-box; 
} 


